import React, { useEffect, useState } from 'react'
// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
// core components
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import MaterialTable from 'material-table';
import { Paper } from '@material-ui/core';

import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import Button from "../../components/CustomButtons/Button";
import DoneIcon from "@material-ui/icons/Done";
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";

const useStyles = makeStyles(styles);

const raw_columns = [
    { title: 'Date', field: 'date' },
    { title: 'Experiment ID', field: 'exp_id' },
    { title: 'Cell lines', field: 'cell_lines' },
    { title: 'Experiment', field: 'experiment' },
    { title: 'Conditions', field: 'conditions' },
    { title: 'Notes', field: 'notes' },
    { title: 'Results', field: 'results' },
    { title: 'Positions', field: 'positions' },
    { title: 'Owner', field: 'owner' },

];



export default function Dashboard(props) {
    const classes = useStyles();
    const [tableData, setTableData] = useState([])
    const [columns, setColumns] = React.useState(raw_columns);
    const [newDialogOpen, setNewDialogOpen] = React.useState(false);
    const [newExpId, setNewExpId] = React.useState('');

    const handleClickNew = () => {
        setNewDialogOpen(true);
    };

    const handleCloseNew = () => {
        setNewDialogOpen(false);
    };

    const handleNewExperiment = () => {
        setNewDialogOpen(false);
        debugger;
        console.log(newExpId);
        fetch('http://127.0.0.1:8080/experiment/'+newExpId+'/', {
            method: 'POST'
        });
        props.history.push('/admin/exp_details/'+newExpId)

    };

    function handleExpIDChange(event) {
        debugger;
        setNewExpId(event.target.value.replace(' ','-'));
    }

    function delete_exp(exp_id){
        fetch('http://127.0.0.1:8080/experiment/'+exp_id+'/', {
            method: 'DELETE'
        });

        return;
    }

    function update_exp(exp){
        fetch('http://127.0.0.1:8080/experiment/'+exp.exp_id+'/', {
            method: 'PATCH',
            body:JSON.stringify(exp)
        });
        // console.log(exp);
        return;
    }


    useEffect(() => {
        fetch("http://127.0.0.1:8080/allexperiments/")
            .then(res => res.json())
            .then(
                (result) => {
                    const res = [];

                    Object.keys(result).map((key) => {
                        const rowData = result[key];
                        // rowData['exp_id'] = key;
                        res.push(rowData);
                    })
                    setTableData(res)
                },
                (error) => {
                }
            )


    }, [])
    return (
        <div>
            <Fab color="secondary"
                 aria-label="add"
                 className={classes.fab}
                 onClick={handleClickNew}
            >
                <AddIcon />
            </Fab>
            <Dialog open={newDialogOpen} onClose={handleCloseNew} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">New Experiment</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Provide experiment ID. Remember - use this ID when uploading results from the client. (Don't use white spaces)
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="input_experiment_id"
                        label="Experiment ID"
                        type="text"
                        value={newExpId}
                        onChange={handleExpIDChange}

                        fullWidth
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseNew} color="danger">
                        Cancel
                    </Button>
                    <Button onClick={handleNewExperiment} color="success">
                        Subscribe
                    </Button>
                </DialogActions>
            </Dialog>


            <Card>
                <CardHeader color="success">
                    <h4 className={classes.cardTitleWhite}>Available Experiments</h4>
                    <p className={classes.cardCategoryWhite}>
                        --------------------------------------------
                    </p>
                </CardHeader>
                <CardBody>
                    <MaterialTable
                        components={{
                            Container: props => <Paper {...props} elevation={0}/>
                        }}
                        onRowClick={(event, rowData) => {
                            // <ExpDetailsPage />
                            console.log(rowData);
                            props.history.push('/admin/exp_details/'+rowData['exp_id'])

                        }
                        }
                        options={{
                            headerStyle: {
                                color: '#4caf50',
                                fontWeight: "300",
                            }
                        }}

                        title=""
                        columns={columns}
                        data={tableData}
                        editable={{
                            // isEditable: rowData => rowData.owner === "Shaked", // only name(a) rows would be editable
                            onRowUpdate: (newData, oldData) =>
                                new Promise((resolve) => {
                                    setTimeout(() => {
                                        resolve();
                                        //TODO: ADD API TO UPDATE EXPERIMENT
                                        if (oldData) {
                                            setTableData((prevState) => {
                                                const data = [...prevState];
                                                update_exp(newData);
                                                data[data.indexOf(oldData)] = newData;
                                                return  data ;
                                            });
                                        }
                                    }, 600);
                                }),
                            onRowDelete: (oldData) =>
                                new Promise((resolve) => {
                                    setTimeout(() => {
                                        resolve();
                                        setTableData((prevState) => {
                                            const data = [...prevState];
                                            delete_exp(oldData.exp_id);
                                            data.splice(data.indexOf(oldData), 1);
                                            return data;
                                        });
                                    }, 600);
                                }),
                        }}
                    />
                </CardBody>
            </Card>
        </div>
    );
}
