import React, { useEffect } from 'react'
// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
// core components
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import GridContainer from "../../components/Grid/GridContainer";
import GridItem from "../../components/Grid/GridItem";
import Card from "../../components/Card/Card";
import CardHeader from "../../components/Card/CardHeader";
import Slider from '@material-ui/core/Slider';
import Checkbox from '@material-ui/core/Checkbox';
import { JsonToTable } from "react-json-to-table";

const useStyles = makeStyles(styles);

export default function ExpResults(props) {
    const classes = useStyles();

    const [gridState, setGridState] = React.useState({angle: 0, time: 0});
    const [expData, setExpData] = React.useState({});
    const [tags, setTags] = React.useState([]);
    const [imagesResults, setImagesResults] = React.useState([]);
    const [timeMarks, setTimeMarks] = React.useState({min:0, max:0, value:0, marks: []});
    const [parsers, setParsers] = React.useState({});



    const handleChangeAngle = (event) => {
        setGridState({angle: event.target.value, time: gridState.time})
    };

    const handleChangeTime = (event,newValue) => {
        setGridState({angle: gridState.angle, time: "time_"+newValue})
        setTimeMarks({...timeMarks,'value':newValue});
    };

    const handleTagChange = (event) => {
        setTags({ ...tags, [event.target.name]: event.target.checked });
    };

    const handleParserChange = (event) => {
        setParsers({ ...parsers, [event.target.name]: event.target.checked });
    };

    function PositionCard(props){
        function hasDataToPresent(data,pos_id){
            console.log(data);
            var has_tags = 0;
            for (const [parser, parser_details] of Object.entries(data)) {
                if (!parsers[parser])
                    continue;


                for (const tag in data[parser].tags) {
                    if (!tags[data[parser].tags[tag]])
                        continue;
                    has_tags++;
                }
                if (has_tags ==0)
                    return false;
                if (data[parser].result_type === 'image_array') {
                    if ((expData[pos_id][parser][gridState.time] === undefined)
                        || (expData[pos_id][parser][gridState.time][gridState.angle] === undefined))
                        continue;
                }
                else if (data[parser].result_type === 'data_frame') {
                    //TODO: Check for something
                }

                return true;

            }
            return false
        }


        if (hasDataToPresent(props.data,props.pos_id))
            return <Card>
                <CardHeader>
                    <h4>Position {props.pos_id.replace('pos_','')}</h4>

                    <h3 className={classes.cardTitle}>
                        {Object.keys(props.data).filter((parser) => {
                            if (!parsers[parser])
                                return false;
                            const has_tags = props.data[parser].tags.some((tag) => {
                                return tags[tag];
                            })
                            if (props.data[parser].result_type === 'image_array') {
                                const has_time_and_angle = props.data[parser][gridState.time] !== undefined
                                    && props.data[parser][gridState.time][gridState.angle] !== undefined;
                                return has_tags && has_time_and_angle;
                            }
                            return false;
                        }).map((parser) => (
                            <img className="photo"  alt="complex" src={props.data[parser][gridState.time][gridState.angle]} />
                        ))}

                        {Object.keys(props.data).filter((parser) => {
                            if (!parsers[parser])
                                return false;
                            const has_tags = props.data[parser].tags.some((tag) => {
                                return tags[tag];
                            })
                            if (props.data[parser].result_type === 'data_frame') {
                                console.log(props.data[parser].locus_centers)
                                //TODO Check something other than just has_tags. (data maybe?)
                                return has_tags;
                            }
                            return false;
                        }).map((parser) => (
                            <JsonToTable json={JSON.parse(props.data[parser].locus_centers)} />
                        ))}
                    </h3>
                </CardHeader>
            </Card>
        return null;
    }

    useEffect(() => {
        fetch("http://127.0.0.1:8080/experiment/" + props.match.params.exp_id)
            .then(res => res.json())
            .then(
                (result) => {
                    //State for experiment data
                    setExpData(result);

                    //Now let's digest the experiment data to show the right controls:

                    //Combine all tags, times and angles
                    const temp_tags = {};
                    const pos_images_results = [];
                    const time_marks_results = [];
                    const parsers_results = {};


                    for (const [pos_id] of Object.entries(result)) {
                        if (!pos_id.startsWith("pos_"))
                            continue;
                        //Add all angles of this position
                        for (const [parser] of Object.entries(result[pos_id])) {
                            if (!parser.startsWith("parser_"))
                                continue;

                            const this_tags = result[pos_id][parser].tags;
                            //Add all tags of this position.
                            this_tags.forEach(function (value) {
                                temp_tags[value] = true
                            });
                            parsers_results[parser] = true;

                            for (const [pos_time] of Object.entries(result[pos_id][parser])) {
                                if (pos_time.startsWith("time_")) {
                                    if (!time_marks_results.includes(pos_time))
                                        time_marks_results.push(pos_time);
                                    for (const [pos_image] of Object.entries(result[pos_id][parser][pos_time])) {
                                        if (!pos_images_results.includes(pos_image))
                                            pos_images_results.push(pos_image)
                                        // pos_images_results.add(pos_image);
                                    }
                                }
                            }
                        }
                    }
                    const time_marks_results_array = [];
                    time_marks_results.forEach(function (value) {
                        time_marks_results_array.push({
                                    value: parseInt(value.substring(5, value.length)),
                                    label: `T${value.substring(5, value.length)}`,
                                });
                    })

                    setImagesResults(pos_images_results);
                    setParsers(parsers_results);
                    setTags(temp_tags);


                    setTimeMarks({
                        min:Math.min(...(time_marks_results_array.map(d => d.value))),
                        max:Math.max(...(time_marks_results_array.map(d => d.value))),
                        value:Math.min(...(time_marks_results_array.map(d => d.value))),
                        marks: time_marks_results_array
                    });

                    setGridState({angle: pos_images_results.values().next().value, time: "time_"+timeMarks.value})

                },
                (error) => {
                }
            )


    }, [props.match.params.exp_id])
  return (
      <div>
          

          <FormControl component="fieldset">
              <FormLabel component="legend">Angle</FormLabel>
              <RadioGroup aria-label="angle" name="angle" value={gridState.angle} onChange={handleChangeAngle}>
                  {Object.keys(imagesResults).map((key) => (
                      <FormControlLabel value={imagesResults[key]} control={<Radio />} label={imagesResults[key]} />
                  ))}
              </RadioGroup>
          </FormControl>

          <FormControl component="fieldset" >
              <FormLabel component="legend">Time</FormLabel>
              <Slider className={classes.timeControl}
              value={timeMarks.value}
              onChange={handleChangeTime}
              step={null}
              valueLabelDisplay="on"
              marks={timeMarks.marks}
              max={timeMarks.max}
              min={timeMarks.min}
              color="secondary"
              />
          </FormControl>
          <FormControl component="fieldset" className={classes.tagControl} >
              <FormLabel component="legend">Tags</FormLabel>
              {Object.keys(tags).map((key) => (
                  <FormControlLabel
                      control={
                          <Checkbox
                              checked={tags[key]}
                              onChange={handleTagChange}
                              name={key}
                              color="secondary"
                          />
                      }
                      label={key}
                  />
              ))}

            </FormControl>
            <FormControl component="fieldset" className={classes.tagControl} >
                <FormLabel component="legend">Parsers</FormLabel>
                {Object.keys(parsers).map((key) => (
                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={parsers[key]}
                            onChange={handleParserChange}
                            name={key}
                            color="secondary"
                        />
                        }
                        label={key}
                    />
                ))}
            </FormControl>


          <GridContainer>
              {Object.keys(expData).filter((key) => {
                  if (!key.startsWith("pos_"))
                      return false;
                return true;
              }).map((key) => (
                  <GridItem xs={12} sm={12} md={4}>
                        <PositionCard data={expData[key]} pos_id={key}/>
                  </GridItem>
              ))}
          </GridContainer>

      </div>
  );
}
