import React, {useEffect} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import EditIcon from '@material-ui/icons/Edit';
import DoneIcon from '@material-ui/icons/Done';
import Iframe from 'react-iframe'

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",

  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
  editButton: {
    color: "#000",
    position: "absolute",
    right: "10px",
    top: "15px"
  },
  doneButton: {
    color: "#fff",
    position: "absolute",
    right: "10px",
    top: "15px"
  }

};

const useStyles = makeStyles(styles);

export default function ExpDetails(props) {
  const classes = useStyles();
  const [editing, setEditing] = React.useState(false);
  const [expData, setExpData] = React.useState({date:'',cell_lines:'',experiment:'',conditions:'',notes:'',results:'',positions:'',owner:'',google_doc:''});

  useEffect(() => {
    fetch("http://127.0.0.1:8080/experiment/" + props.match.params.exp_id)
        .then(res => res.json())
        .then(
            (result) => {
              //State for experiment data
              setExpData(result);


            })
  }, [props.match.params.exp_id])

  function startEdit(){
    setEditing(!editing);
  }
  function doneEdit(){
    setEditing(!editing);
    fetch('http://127.0.0.1:8080/experiment/'+expData.exp_id+'/', {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json'
      },
      body:JSON.stringify(expData)
    });
    console.log(expData);
    return;

  }
  function EditingButton(props) {
    const isEditing = props.isEditing;
    if (isEditing) {
      return <Button
          variant="contained"
          color="success"
          className={classes.doneButton}
          onClick={doneEdit}
          startIcon={<DoneIcon />}
      >
        Done
      </Button>;
    }
    return <Button
        variant="contained"
        color="white"
        className={classes.editButton}
        onClick={startEdit}
        startIcon={<EditIcon />}
    >
      Edit
    </Button>;
  }

  function ExperimentTitle(props){
    const isEditing = props.isEditing;
    if (isEditing) {
      return "Edit Profile";
    }
    return "Experiment "+expData.exp_id+" Details"
  }

  function GoogleDoc(props){
    const doc_url = props.url;
    if (doc_url === undefined)
      return null;
    if (doc_url.includes("docs.google.com")) {
      return       <Iframe url={doc_url}
                           width="100%"
                           height="800px"
                           position="relative"/>

    }
    return null;
  }

  function handleChange(event,value) {
    setExpData({...expData,[event.target.name]:event.target.value})}


  const raw_columns = [
    { title: 'Date', field: '' },
    { title: '', field: '' },
    { title: 'Experiment', field: '' },
    { title: '', field: '' },
    { title: '', field: '' },
    { title: 'Results', field: '' },
    { title: 'Positions', field: '' },
    { title: 'Student', field: '' },

  ];


  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}><ExperimentTitle isEditing={editing}/></h4>
              <p className={classes.cardCategoryWhite}>Experiment details</p>
              <EditingButton isEditing={editing} />
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <CustomInput
                    labelText="Date"
                    // id="company-disabled"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      name: 'date',
                      disabled: !editing,
                      value: expData.date,
                      onChange:handleChange
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <CustomInput
                      labelText="Cell lines"
                      // id="company-disabled"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        name: 'cell_lines',
                        disabled: !editing,
                        value: expData.cell_lines,
                        onChange:handleChange


                      }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <CustomInput
                      labelText="Experiment"
                      // id="company-disabled"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        name: 'experiment',
                        disabled: !editing,
                        value: expData.experiment,
                        onChange:handleChange


                      }}
                  />
                </GridItem>

                <GridItem xs={12} sm={12} md={3}>
                  <CustomInput
                      labelText="Conditions"
                      // id="company-disabled"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        name: 'conditions',
                        disabled: !editing,
                        value: expData.conditions,
                        onChange:handleChange


                      }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <CustomInput
                      labelText="Positions"
                      // id="company-disabled"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        name: 'positions',
                        disabled: !editing,
                        value: expData.positions,
                        onChange:handleChange


                      }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <CustomInput
                      labelText="Owner"
                      // id="company-disabled"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        name: 'owner',
                        disabled: !editing,
                        value: expData.owner,
                        onChange:handleChange

                      }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <CustomInput
                      labelText="Google Doc"
                      // id="company-disabled"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        name: 'google_doc',
                        disabled: !editing,
                        value: expData.google_doc,
                        onChange:handleChange

                      }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <InputLabel style={{ color: "#AAAAAA" }}>Notes</InputLabel>
                  <CustomInput
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      name: 'notes',
                      disabled: !editing,
                      multiline: true,
                      rows: 5,
                      value:expData.notes,
                      onChange:handleChange

                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <InputLabel style={{ color: "#AAAAAA" }}>Results</InputLabel>
                  <CustomInput
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        name: 'results',
                        disabled: !editing,
                        multiline: true,
                        rows: 5,
                        value:expData.results,
                        onChange:handleChange

                      }}
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
      <GoogleDoc url={expData.google_doc}/>

    </div>
  );
}
