import React, { useEffect, useState } from 'react'
// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
// core components
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import MaterialTable from 'material-table';
import { Paper } from '@material-ui/core';

import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";

const useStyles = makeStyles(styles);

const raw_columns = [
    { title: 'Date', field: 'date' },
    { title: 'Experiment ID', field: 'exp_id' },
    { title: 'Cell lines', field: 'cell_lines' },
    { title: 'Experiment', field: 'experiment' },
    { title: 'Conditions', field: 'conditions' },
    { title: 'Notes', field: 'notes' },
    { title: 'Results', field: 'results' },
    { title: 'Positions', field: 'positions' },
    { title: 'Owner', field: 'owner' },

];



export default function ExpList(props) {
    const classes = useStyles();
    const [tableData, setTableData] = useState([])
    const [columns, setColumns] = React.useState(raw_columns);

    function delete_exp(exp_id){
        fetch('http://127.0.0.1:8080/experiment/'+exp_id+'/', {
            method: 'DELETE'
        });

        return;
    }

    function update_exp(exp){
        fetch('http://127.0.0.1:8080/experiment/'+exp.exp_id+'/', {
            method: 'PATCH',
            body:JSON.stringify(exp)
        });
        // console.log(exp);
        return;
    }


    useEffect(() => {
        fetch("http://127.0.0.1:8080/allexperiments/")
            .then(res => res.json())
            .then(
                (result) => {
                    const res = [];

                    Object.keys(result).map((key) => {
                        const rowData = result[key];
                        // rowData['exp_id'] = key;
                        var has_results = Object.keys(rowData).filter(function(item){
                            return typeof item == 'string' && item.indexOf("pos_") > -1;
                        }).length;
                        if (has_results > 0)
                            res.push(rowData);
                    })
                    setTableData(res)
                },
                (error) => {
                }
            )


  }, [])
  return (
    <div>
        <Card>
        <CardHeader color="success">
          <h4 className={classes.cardTitleWhite}>Available Experiments with Results</h4>
          <p className={classes.cardCategoryWhite}>
            --------------------------------------------------------------------
          </p>
        </CardHeader>
          <CardBody>
            <MaterialTable
                components={{
                    Container: props => <Paper {...props} elevation={0}/>
                }}
                onRowClick={(event, rowData) => {
                    // <ExpDetailsPage />
                        props.history.push('/admin/exp_results/'+rowData['exp_id'])

                }
                }
                options={{
                    headerStyle: {
                        color: '#4caf50',
                        fontWeight: "300",
                    }
                }}

                title=""
                columns={columns}
                data={tableData}
            />
        </CardBody>
      </Card>
    </div>
  );
}
